/* FinalPage.css */

.final-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #fff;
  color: black;
}

.header {
  background-color: #987d80;
  color: #fff;
  padding: 15px;
  text-align: center;
}

.main-content {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.booking-slots {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 800px;
  flex-wrap: wrap;
}

.court {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid #000;
  margin: 10px;
  flex: 1;
  min-width: 300px;
  max-width: 300px;
}

.court h3 {
  margin-bottom: 15px;
  color: #000;
}

.slots {
  display: flex;
  flex-direction: column;
}

.slot {
  padding: 10px;
  margin-bottom: 10px;
  background: #000;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
}

.slot:hover {
  background: #333;
}

.footer {
  background-color: #987d80;
  color: #fff;
  text-align: center;
  padding: 10px;
}

.button {
  padding: 8px 16px;  /* Adjusted padding for normal size */
  background-color: rosybrown;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px; /* Adjusted font size for normal appearance */
  margin-top: 10px; /* Adjusted margin */
  width: 250px; /* Set width to auto to avoid full-width */
}





  