/* PaymentPage.css */

.payment-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f9f9f9;
  height: 100vh;
}

.payment-container h2 {
  margin-bottom: 20px;
}

.payment-container p {
  margin-bottom: 20px;
}

.payment-details {
  width: 100%;
  max-width: 400px;
}

.payment-details form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
    padding: 8px 16px;  /* Adjusted padding for normal size */
    background-color: #bd6c74;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px; /* Adjusted font size for normal appearance */
    margin-top: 20px; /* Adjusted margin */
  }
  
  button:hover {
    background-color: #9f6b71;
  }

button[type="button"] {
  background-color: #6c757d;
  margin-top: 10px;
}

button[type="button"]:hover {
  background-color: #5a6268;
}
